import Mock from 'mockjs';

/**
 * 
 * URL: /api/dics
 * METHOD:GET
 */
Mock.mock(/\/api\/dics$/, {
  success: true,
  data: [
    { group: 'RELATION_SHIP', name: '', value: '1' },
    { group: 'RELATION_SHIP', name: '', value: '2' },
    { group: 'DISABLED_TYPE', name: '', value: '1' },
    { group: 'DISABLED_TYPE', name: '', value: '2' },
    { group: 'DISABLED_TYPE', name: '', value: '3' },
    { group: 'DISABLED_LEVEL', name: '', value: '1' },
    { group: 'DISABLED_LEVEL', name: '', value: '2' },
    { group: 'DISABLED_LEVEL', name: '', value: '3' },
    { group: 'DEGREE_TYPE', name: '', value: '1' },
    { group: 'DEGREE_TYPE', name: '', value: '2' },
    { group: 'DEGREE_TYPE', name: '', value: '3' },
    { group: 'EDUCATION_LEVEL', name: '', value: '1' },
    { group: 'EDUCATION_LEVEL', name: '', value: '2' },
    { group: 'EDUCATION_LEVEL', name: '', value: '3' },
    { group: 'JOB_TYPE', name: '', value: '1' },
    { group: 'JOB_TYPE', name: '', value: '2' },
    { group: 'JOB_TYPE', name: '', value: '3' },
    { group: 'ABILITY_DOMAIN', name: '', value: '101' },
    { group: 'ABILITY_DOMAIN', name: '', value: '102' },
    { group: 'ABILITY_DOMAIN', name: '', value: '103' },
  ],
});



