import Mock from 'mockjs';

const abilityIds=['A1','A2','A3','A4','A5','B1','B2','B3','B4','B5','B6','C1','C2','C3','C4','C5','C6'];

/**
 * 
 * URL: /api/occupations/{occupationId}
 * METHOD:GET
 */
Mock.mock(/\/api\/occupations\/\w+$/, {
  success: true,
  data: {
    id: '@id',
    'name|1': ['', '', '', '', '', '', ''],
    desc: '@cparagraph',
    'necessaryAbility|5': [{'abilityId|1':abilityIds,'criterionScore|1':['A','B','C']}], //ID
    'secondaryAbility|5': [{'abilityId|1':abilityIds,'criterionScore|1':['A','B','C']}], // ID
  },
});

/**
 * 
 * URL: /api/occupations/
 * METHOD:GET
 */
Mock.mock(/\/api\/occupations(\?\S*)*$/, {
  success: true,
  data: {
    'list|10': [
      {
        id: '@id',
        'name|1': ['', '', '', '', '', '', ''],
        desc: '@cparagraph',
        'necessaryAbility': abilityIds.slice(0,5), //ID
        'secondaryAbility': abilityIds.slice(0,8), //ID
      },
    ],
    total: 20,
  },
});



