import React from 'react';
import PropTypes from 'prop-types';
import { Card, Row, Col, Icon } from 'antd';
import styles from 'style/Trainee.scss';
import CSSModules from 'react-css-modules';

const TraineeInfo = ({ trainee, dicHelper: { getDicName } }) => {
  return (
    <div className="content-inner" styleName="detail">
      <Card bordered={false} noHovering="false">
        <div styleName="face">
          <img alt={''} src={'/images/face.png'} />
        </div>
        <div styleName="info">
          <p>
            {trainee.name}
          </p>
          <ul>
            <li>
              <Icon
                type={trainee.sex ? 'man' : 'woman'}
                style={{ color: '#108ee9' }}
              />
              {trainee.sex ? '' : ''}
            </li>
            <li>
              {trainee.age} ({trainee.birthday})
            </li>
            <li>
              {trainee.guardian}({trainee.relationship})
            </li>
          </ul>
          <ul>
            <li>
              <span>
                <Icon type="mobile" /> {trainee.phone}
              </span>
            </li>
            <li>
              <span>
                <Icon type="mail" /> {trainee.mail}
              </span>
            </li>
          </ul>
        </div>
      </Card>
      <Card title="" bordered={false} noHovering="false">
        <Row>
          <Col span="5">:</Col>
          <Col>
            {trainee.idCard}
          </Col>
        </Row>
        <Row>
          <Col span="5">:</Col>
          <Col>
            {trainee.address}
          </Col>
        </Row>
      </Card>
      <Card title="" bordered={false} noHovering="false">
        <Row>
          <Col span="5">:</Col>
          <Col span="7">
            {getDicName('DISABLED_TYPE',trainee.disabledType)}
          </Col>
          <Col span="5">:</Col>
          <Col span="7">
            {getDicName('DISABLED_LEVEL',trainee.disabledLevel)}
          </Col>
        </Row>
        <Row>
          <Col span="5">:</Col>
          <Col span="19">
            {trainee.disabledReason}
          </Col>
        </Row>
        <Row>
          <Col span="5">:</Col>
          <Col span="19">
            {trainee.otherDisabled}
          </Col>
        </Row>
      </Card>
      <Card title="" bordered={false} noHovering="false">
        <Row>
          <Col span="5">:</Col>
          <Col span="7">
            {getDicName('DEGREE_TYPE',trainee.degree)}
          </Col>
          <Col span="5">:</Col>
          <Col span="7">
            {getDicName('EDUCATION_LEVEL',trainee.educationLevel)}
          </Col>
        </Row>
        <Row>
          <Col span="5">:</Col>
          <Col span="19">
            {trainee.haveTrained === 1 ? '' : ''}
          </Col>
        </Row>
        <Row>
          <Col span="5">:</Col>
          <Col span="19">
            {trainee.trainedIntro}
          </Col>
        </Row>
      </Card>
      <Card title="" bordered={false} noHovering="false">
        <Row>
          <Col span="5">:</Col>
          <Col span="19">
            {trainee.isWorked === 1 ? '' : ''}
          </Col>
        </Row>
        <Row>
          <Col span="5">:</Col>
          <Col span="19">
            {trainee.workedIntro}
          </Col>
        </Row>
        <Row>
          <Col span="5">:</Col>
          <Col span="19">
            {trainee.expectWork}
          </Col>
        </Row>
      </Card>
      <Card title="" bordered={false} noHovering="false">
        <Row>
          <Col span="3">:</Col>
          <Col span="5">
            {trainee.father}
          </Col>
          <Col span="3">:</Col>
          <Col span="5">
            {getDicName('DEGREE_TYPE',trainee.fatherDegree)}
          </Col>
          <Col span="3">:</Col>
          <Col span="5">
            {getDicName('JOB_TYPE',trainee.fatherJob)}
          </Col>
        </Row>
        <Row>
          <Col span="3">:</Col>
          <Col span="5">
            {trainee.mother}
          </Col>
          <Col span="3">:</Col>
          <Col span="5">
            {getDicName('DEGREE_TYPE',trainee.motherDegree)}
          </Col>
          <Col span="3">:</Col>
          <Col span="5">
            {getDicName('JOB_TYPE',trainee.motherJob)}
          </Col>
        </Row>
      </Card>
      <Card title="" bordered={false} noHovering="false">
        <Row>
          <Col span="3">:</Col>
          <Col span="8">
            {trainee.creator}
          </Col>
          <Col span="3">:</Col>
          <Col span="8">
            {trainee.createTime}
          </Col>
        </Row>
        <Row>
          <Col span="3">:</Col>
          <Col span="8">
            {trainee.modifier}
          </Col>
          <Col span="3">:</Col>
          <Col span="8">
            {trainee.modifyTime}
          </Col>
        </Row>
      </Card>
    </div>
  );
};
TraineeInfo.propTypes = {
  trainee: PropTypes.object.isRequired,
};

export default CSSModules(TraineeInfo, styles);



